<template>
  <div
    style="height:100vh;font-size: 0.8rem;"
    v-if="currentInfo"
  >
    <div class="pay-room-box">
      <div class="pay-room">
        <div>账单状态</div>
        <div>
          <span :class="currentInfo.payStatus == 0?'pay_r':'pay_g'">{{ currentInfo.payStatus == 0?'未支付':'已支付' }}</span>
        </div>
      </div>
      <div class="pay-room">
        <div>收租日期</div>
        <div>{{ currentInfo.startTime | dataFilter }}</div>
      </div>
      <div class="pay-room">
        <div>缴费周期</div>
        <div>{{ currentInfo.startTime | dataFilter }}&ensp;至&ensp;{{ currentInfo.endTime | dataFilter }}</div>
      </div>
      <div class="h10"></div>
      <div class="pay-room">
        <div>账单金额</div>
        <div>{{ currentInfo.roomPrice || '0' }}元</div>
      </div>
      <div class="h10"></div>
      <div
        class="pay-room"
        v-if="currentInfo.payTime"
      >
        <div>到账时间</div>
        <div>{{ currentInfo.payTime }}</div>
      </div>
      <div
        class="pay-room"
        v-if="objPayments && currentInfo.payment"
      >
        <div>支付方式</div>
        <div>{{ objPayments[currentInfo.payment] }}</div>
      </div>
      <div
        class="pay-room"
        v-if="currentInfo.payeeAccount"
      >
        <div>收款账户</div>
        <div>{{ currentInfo.payeeAccount }}</div>
      </div>
      <div class="h10"></div>
    </div>
    <div class="room-bottom">
      <div class="room-bottom-left">
        <p class="c_888 f12">费用总计</p>
        <p class="f16">&yen;{{ currentInfo.roomPrice }}</p>
      </div>
      <van-button
        round
        class="room-bottom-right"
        style="background:#b2b2b2;color:#fff;border:#b2b2b2;"
        v-if="currentInfo.payStatus == 1"
      >已到账</van-button>
      <van-button
        round
        class="room-bottom-right"
        @click="comeShow = true"
        v-if="currentInfo.payStatus == 0"
      >到账</van-button>
    </div>
    <van-popup
      v-model="comeShow"
      class="show-box"
    >
      <div class="show-box-content">
        <p class="f22">到账</p>
        <div class="pay-room none">
          <span>到账时间</span>
          <div class="border">
            <input
              @click="comeShowTime = true"
              class="center"
              readonly
              placeholder="请选择"
              type="text"
              v-model="comeTime"
            >
          </div>
        </div>
        <div class="pay-room none">
          <span>收款账户</span>
          <div
            class="border display"
            @click="showPicker = true"
          >
            <input
              class="center"
              readonly
              placeholder="请选择收款账户"
              type="text"
              v-model="payeeAccountIdValue"
            >
            <van-icon name="arrow-down" />
          </div>
        </div>
        <van-button
          class="show-button"
          @click="save"
        >确定</van-button>
      </div>
    </van-popup>
    <van-calendar
      v-model="comeShowTime"
      :max-date="maxDate"
      :min-date="minDate"
      @confirm="onConfirm"
    />
    <van-popup
      v-model="showPicker"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="users"
        @confirm="onConfirmPayee"
        @cancel="showPicker = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessHotelPayeeAccountByhotel } from '@/api/api_business';
import { GetBusinessCooperRentalBill, PostbusinessCooperRentalBillPayOffline } from '@/api/api_business';
import { Toast } from 'vant';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            currentInfo: null,
            roomLists: [],
            roomAllPrice: '',
            roomAllDeposit: '',
            comeShow: false,
            comeTime: dayjs().format('YYYY-MM-DD'),
            dictLabel: '',
            comeShowTime: false,
            maxDate: new Date(),
            minDate: '',
            date: '',
            payeeAccountIdValue: null,
            users: [],
            showPicker: false,
            objPayments: {}
        };
    },
    created() {
        this.$store.commit('setHeader', '账单明细');
        this.getBusinessPayment();
        this.minDate = new Date(dayjs().subtract(2, 'month'));
    },
    methods: {
        getBusinessPayment() {
            let c_hotelId = localStorage.getItem('c_hotelId');
            GetBusinessHotelPayeeAccountByhotel({ hotelId: c_hotelId }).done((res) => {
                this.userAll = res.data.filter((item) => item.payment >= '5' || item.account);
                this.users = res.data.filter((item) => item.payment >= '5' || item.account).map((item) => item.accountName);
            });
            GetSystemDictDataByType({ dictType: 'business_payment' }).done((res) => {
                res.data.map((item) => {
                    this.objPayments[item.dictValue] = item.dictLabel;
                });
                this.getCurrenInfo(this.$route.query.cooperRentalBillId);
            });
        },
        getCurrenInfo(id) {
            GetBusinessCooperRentalBill({ cooperRentalBillId: id }).done((result) => {
                this.currentInfo = result.data;
            });
        },
        onConfirm(date) {
            this.comeShowTime = false;
            this.comeTime = dayjs(date).format('YYYY-MM-DD');
        },
        onConfirmPayee(val) {
            this.payeeAccountIdValue = val;
            this.payeeAccountId = this.userAll.find((item) => item.accountName == val).id;
            this.showPicker = false;
        },
        save() {
            if (!this.payeeAccountId) {
                Toast({
                    message: '请选择收款账户',
                    duration: 900
                });
                return;
            }
            PostbusinessCooperRentalBillPayOffline({ cooperRentalBillId: this.$route.query.cooperRentalBillId, payeeAccountId: this.payeeAccountId, payTime: this.comeTime }).done((res) => {
                this.comeShow = false;
                Toast({
                    message: res.msg,
                    duration: 2000
                });
                this.getCurrenInfo(this.$route.query.cooperRentalBillId);
            });
        }
    }
};
</script>
<style scoped>
.pay-room-box {
    background: #fff;
    margin-top: 0.9rem;
    color: #000;
}
.pay-room {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem;
    border-bottom: 1px solid #e5e5e5;
}
.room-details-scroll {
    max-height: 14.5rem;
    overflow-y: scroll;
}
.h10 {
    height: 10px;
    background: #f6f6f6;
}
.left {
    text-align: left;
    padding: 0.5rem;
}
.center {
    text-align: center;
    width: 60%;
}
.c_888 {
    color: #888;
    font-weight: normal;
}
.c_000 {
    color: #000;
    font-weight: 600;
}
.f12 {
    font-size: 0.6rem;
}
.f16 {
    font-size: 0.8rem;
    font-weight: 600;
}
.f22 {
    font-weight: 600;
    font-size: 1.1rem;
    padding: 0.5rem 0 1rem;
}
.pay_g {
    color: #02bc6c;
}
.pay_r {
    color: #ff5257;
}
.none {
    border: none;
    line-height: 40px;
    padding: 0.5rem 0;
    width: 100%;
}
.border {
    border: 1px solid #d7d7db;
    border-radius: 5px;
    width: 60%;
}
.display {
    display: flex;
    align-items: center;
    justify-content: center;
}
/* 费用总计 */
.room-bottom {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 2rem;
    background: #fff;
    width: calc(100% - 1rem);
    text-align: left;
    align-items: center;
}
.room-bottom-left {
    line-height: 1rem;
}
.room-bottom-right {
    width: 22%;
}
.van-button {
    height: 30px;
    border: 1px solid #b2b2b2;
    color: #000;
}
/* 弹出框 */
.show-box {
    width: 90%;
    border-radius: 10px;
    font-size: 0.9rem;
    color: rgba(0, 0, 1);
}
.show-box-content {
    padding: 1rem;
}
.show-button {
    width: 100%;
    background: #ff7d41;
    color: #fff;
    height: 2.5rem;
    margin-top: 1rem;
}
</style>



